<template >
    <section>
        <div class=" theme-primary partition">

            <span class="p-name text-white">
                New Product Type Request
            </span>
            <span class="p-actions float-right">
                <a class="p-action-btn text-white" href="/AssetRequisition/NewAssetRequisitionListing"
                    data-toggle-tooltip="tooltip" data-original-title="Back to list"><i
                        class="fa fa-angle-double-left pr-2"></i><br><span>Back to list</span></a>
            </span>

        </div>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 ">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 px-0 pb-2">
                <div class="d-flex flex-wrap">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 ">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 ">
                            <div class="row">
                                <div class="col-lg-3 ">

                                    <div class="form-group">
                                        <label>{{ $t('Location') }}<span class="text-danger">*</span></label>
                                        <Field name="Location" class="w-100" :rules="{ 'required': true }"
                                            v-slot="{ errors }">
                                            <tree-view v-model="LocationValue" :options="options"
                                                placeholder="Select Location" :show-count="true"
                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                            </tree-view>
                                            <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
                                                errors[0]
                                            }}</span>

                                        </Field>
                                    </div>
                                </div>

                                <div class="col-lg-3">
                                    <div class="form-group">

                                        <label> Product Name:<span class="text-danger">*</span></label>
                                        <Field name="itemName">
                                            <input v-model="txtproductName" maxlength="100" type="text"
                                                v-bind:class="{ 'form-control': true }" />

                                        </Field>
                                    </div>
                                </div>


                                <div class="col-lg-3">
                                    <div class="form-group">
                                        <label>Product Type:</label>
                                        <div class="input-group">
                                            <select id="AssetTypeFilter" name="AssetTypeId" class="form-control" value="0">
                                                <option value="">Select</option>
                                                <option value="837">Android Mobile Devices</option>
                                                <option value="1647">asdf</option>
                                                <option value="1648">asdfasdf</option>
                                                <option value="1594">dsds</option>
                                                <option value="838">Heavy metal</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-lg-3">
                                    <div class="form-group">
                                        <label> Requested Quantity:<span class="text-danger">*</span></label>
                                        <Field name="Quantity">
                                            <input v-model="txtrequestQuantity" type="text" maxlength="8"
                                                v-bind:class="{ 'form-control': true }" />

                                        </Field>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3">
                            <div class="form-group">
                                <label for="Description">{{ $t('Description') }}<span class="text-danger">*</span></label>
                                <Field name="description" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <textarea name="description" type="text" v-model="txtDescription" class="form-control"
                                        placeholder="Description" :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                        rows="5" maxlength="250"></textarea>
                                    <span v-if="errors[0]" name="description" class="invalid-feedback d-block">{{ errors[0]
                                    }}</span>
                                </Field>
                            </div>

                        </div>
                        <AddQuotes></AddQuotes>
                        <OnlinePurchase></OnlinePurchase>
                        <div class="col-lg-12 mb-3">
                            <div class="row flex-row-reverse">
                                <div class="col-lg-6 text-right">

                                    <button type="button" class="btn btn-success btnSave pr-2 mr-2" value="Save"
                                        title="Save" status="ACT"><i class="fa fa-save pr-2"></i> Save </button>
                                    <button type="button" class="btn btn-success statuschange pr-2 mr-2"
                                        value="Save As Draft" title="Save" status="Draft"><i class="fa fa-save pr-2 "></i>
                                        Save As Draft </button>
                                    <a id="Cancel" title="" class="btn btn-danger mr-2"
                                        href="/AssetRequisition/NewAssetRequisitionListing" data-toggle-tooltip="tooltip"
                                        data-original-title="Cancel"><i class="fa fa-close pr-2"></i> Cancel </a>
                                </div>
                                <div class="col-lg-6 py-2">
                                    <span class="mandatory">Fields marked with an asterisk (*) are mandatory.</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import AddQuotes from './AddQuotes.vue';
import OnlinePurchase from './OnlinePurchase.vue'
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import DataService from '../../service/DataService';

export default {
    components: {
        AddQuotes,
        OnlinePurchase,
        'tree-view': Treeselect
    },
    data() {
        return {
            options: [],
            LocationValue: '',
            txtDescription: '',
            txtrequestQuantity: null,
            txtproductName: ''
        }
    },
    created() {
        var vm = this;
        vm.GetLocationByCompanyId();
    },
    methods: {

        GetLocationByCompanyId: async function () {
            var vm = this;
            var url = `reqForm=assetassignment`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },

    }

}
</script>
<style>
.vue-treeselect__control {
    border: none;
    height: 24px;
    padding: 0;
}

.vue-treeselect--searchable .vue-treeselect__input-container {
    padding: 0 !important;
}

.vue-treeselect__placeholder {
    color: #303030 !important;
}

.vue-treeselect__control-arrow {
    color: #000;
}

.vue-treeselect__placeholder {
    line-height: 24px;
}
</style>
